/* reset - start */

body,
ul,
ol,
p {
  padding: 0;
  margin: 0;
}

ul,
ol {
  list-style: none;
}

a,
a:hover {
  color: inherit;
  text-decoration: none;
}

.a11y-hidden {
  display: none;
}
/* reset - end */

#myMap {
  width: 100%;
  height: 100vh;
}

header {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translate(-50%, 3px);
  z-index: 2;
  width: 50%;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.85);
  color: #333;
  text-align: center;
  height: 60px;
  padding-top: 8px;
  box-sizing: border-box;
  box-shadow: rgb(0 0 0 / 15%) 0px 2px 2px 0px;
}

header span {
  display: block;
}

nav {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  overflow: hidden;
  padding: 20px;
  box-sizing: border-box;
}

.btn {
  background-color: #fff;
  border-radius: 50%;
  border: none;
  width: 40px;
  height: 40px;
  box-shadow: rgb(0 0 0 / 15%) 0px 2px 2px 0px;
  font-size: 8px;
  cursor: pointer;
  text-align: center;
}

.btn-current-location {
  float: left;
}

.btn-edu-search {
  float: right;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 20;
}

.modal .modal-dimmed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}

.modal-container {
  position: absolute;
  top: 2.5%;
  left: 0;
  width: 100%;
  border-radius: 30px 30px 0 0;
  height: calc(100% - 2.5%);
  background-color: rgba(255, 255, 255, 0.85);
  backdrop-filter: saturate(180%) blur(20px);
}

.modal-header {
  text-align: center;
}

.modal-header h1 {
  font-size: 20px;
}

.modal-header .modal-close {
  position: absolute;
  top: 13px;
  left: 15px;
  background-color: transparent;
  border: none;
}

.modal-body {
  padding: 10px;
}

.modal input[type="search"] {
  background-color: #efefef;
  border: none;
  width: 100%;
  height: 40px;
  padding: 10px;
  font-size: 16px;
  color: #aaa;
}
.modal input[type="search"]::placeholder {
  color: #aaa;
}

.search-list > li {
  padding-top: 20px;
}

.overlay_info {
  border-radius: 6px;
  margin-bottom: 12px;
  float: left;
  position: relative;
  border: 1px solid #ccc;
  border-bottom: 2px solid #ddd;
  background-color: #fff;
}

.overlay_info:nth-of-type(n) {
  border: 0;
  box-shadow: 0px 1px 2px #888;
}

.overlay_info .title {
  display: block;
  background: rgb(0 181 239);
  text-decoration: none;
  color: #fff;
  padding: 12px 36px 12px 14px;
  border-radius: 6px 6px 0 0
}

.overlay_info .desc {
  padding: 14px;
  position: relative;
  width: 190px;
}

.overlay_info img {
  vertical-align: top;
}

.overlay_info .address {
  font-size: 12px;
  color: #333;
  white-space: normal
}

.overlay_info .btn-close {
  position: absolute;
  top: 14px;
  right: 10px;
  border: none;
  background-color: transparent;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
}

.overlay_info:after {
  content: '';
  position: absolute;
  margin-left: -11px;
  left: 50%;
  bottom: -12px;
  width: 22px;
  height: 12px;
  background: url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white.png) no-repeat 0 bottom;
}
